.logo{
    width: 230px;
    a{
        display: block;
    }
    img{
        width: 100%;
        border: none;
    }
}
.BtnMenu{
    display: none;
    margin-right: 20px;
    .icon-1-3_menu{
        font-size: 16px;
        color: var(--mainColor);
    }

}
.header{
    font-size: 14px;
    &__top{
        &-inner{
            display: flex;
            align-items: center;
            justify-content: space-between;
        }
        &-left, &-right{
            display: flex;
            align-items: center;
        }
        &-right{
            .contact{
                margin-left: 34px;
                margin-right: 51px;
            }
            .popup-desc-parent span:first-of-type {
                display: inline-block;
            }
            .basket {
                .popup-desc{
                    left: -10px;
                }
            }
            .logIn span {
                display: inline-block;
            }
            .logIn {
                padding: 3px 0;
                .popup-desc {
                    width: 127px;
                }
            }
            .popup-desc{
                top: auto;
                bottom: -34px;
                &::after{
                    transform: rotate(180deg);
                    top: auto;
                    bottom: calc(100% - 1px);
                }
            }
        }
        .basket__inner span:nth-of-type(2) {
            margin-bottom: 10px;
        }
    }
    &__bottom{
        background-color: var(--Wedgewood);
        &-inner{
            display: flex;
            align-items: center;
            justify-content: space-between;
        }
        &-left{
            .menu{
                align-items: center;
                &> li{
                    display: flex;
                    align-items: center;
                    color: var(--White);
                    font-size: 14px;
                    padding: 0 30px;
                    transition: all .3s ease-out;
                    &> a{
                        color: var(--White);
                        font-weight: 600;
                        transition: all .3s ease-out;
                    }
                    i{
                        margin-right: 10px;
                        &.icon-1-3_menu{
                            font-size: 14px;
                        }
                    }
                    &.arrowDown{
                        &::after{
                            font-size: 7px;
                            right: 7px;
                        }
                    }
                    &:first-child{
                        background-color: var(--Flamenco);
                        padding: 20.5px 50px 20px 30px;
                        &> i{
                            display: inline-block;
                            width: 22px;
                            margin-right: 20px;
                            &::before{
                                transition: all .3s ease-out;
                            }
                        }
                        &:hover{
                            background-color: var(--CatalinaBlue);
                            transition: all .3s ease-out;
                            &> i{
                                &::before{
                                    content: "\E005";
                                    transition: all .3s ease-out;
                                }
                            }
                        }
                        &.arrowDown{
                            &::after{
                                right: 28px;
                            }
                        }
                        &> .dropdown{
                            width: 353px;
                            padding-left: 0;
                            padding-right: 0;
                        }
                    }
                    &:not(:first-of-type){
                        &:hover{
                            color: var(--Flamenco);
                            transition: all .2s ease-out;
                            &> a{
                                 color: var(--Flamenco);
                                 transition: all .3s ease-out;
                            }
                        }
                    }
                    &:nth-child(3), &:nth-child(2){
                        &::before{
                            top: 100%;
                            left: 0;
                            width: 100%;
                            position: absolute;
                            content: "";
                            height: 20px;
                            background-color: transparent;
                        }
                        .dropdown{
                            top: calc(100% + 20px);
                        }
                    }
                    &:nth-child(3){
                        .dropdown{
                            width: 610px;
                            
                        }
                    }
                    &:nth-child(2){
                        .dropdown{
                            width: 353px;
                            padding-right: 0;
                            padding-left: 0;
                            a{
                                font-size: 13px;
                            }
                        }
                        li {
                            padding-right: 30px;
                            padding-left: 27px;
                            &:after {
                                right: 20px;
                            }
                        }
                        .arrowDown {
                            padding-right: 38px;
                            .dropdown {
                                top: -20px;
                                padding-bottom: 24px;
                                li {
                                    padding-top: 7.5px;
                                    padding-bottom: 7.5px;
                                }
                            }
                        }
                    }
                }
            }
        }
        &-right{
            display: flex;
            align-items: center;
            form{
                width: 48.177vw;
            }
            .input-wrap{
                margin: 9px 0 9px 30px;
            }
            .Btn-Flamenco{
                margin: 9px 0;
            }
        }
    }
    &-fixed{
        background-color: var(--Wedgewood);
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        display: none;
        z-index: 5;
        &__inner{
            display: flex;
            align-items: center;
            justify-content: space-between;
            font-size: 14px;
            .BtnMenu{
                display: none;
            }
            .logo{
                width: 132px;
                margin-right: 200px;
            }
            .header__bottom-left .menu> li:first-child{
                padding: 20px 37px 19px 20px;
                &.arrowDown::after{
                    right: 15px;
                }
            }
            .header__bottom-left .menu> li{
                font-size: 12px;
                padding: 0 15px;
                &.arrowDown::after{
                    right: -2px;
                }
            }
            .Btn-Flamenco {
                font-size: 14px;
            }
            .header__bottom-left .menu> li:nth-child(3) .dropdown{
                width: 237px;
            }
            .contact{
                display: flex;
                align-items: center;
                a{
                    color: #fff;
                    margin-bottom: 0;
                    margin-right: 25px;
                }
            }
            .header__bottom-left{
                display: flex;
                align-items: center;
            }
            .header__bottom-right .Btn-Flamenco{
                margin: 0 30px 0 0;
            }
            .header__bottom-right {
                flex-grow: 1;
                justify-content: space-between;
                div:last-child {
                    display: flex;
                    align-items: center;
                }
                .popup-desc-parent span:first-of-type {
                    display: inline-block;
                }
                .logIn span {
                    display: inline-block;
                }
                .list-line {
                    height: 53px;
                }
            }
            .list-line li{
                padding: 14px 21px 13px;
                border-left-color: rgba(232, 232, 232, .5);
                span:not(.popup-desc){
                    color: var(--White);
                }
                &:hover{
                    .basket span:not(.popup-desc), .logIn span:not(.popup-desc){
                        color: var(--Flamenco);
                    }
                }
                .comparison i, .logIn i{
                    color: var(--White);
                }
                .basket {
                    .popup-desc{
                        left: -11px;
                    }
                }
                .logIn{
                    .popup-desc{
                        bottom: -40px;
                    }
                }
                .popup-desc{
                    white-space: nowrap;
                }
            }
        }
        .header__bottom-right{
            .popup-desc{
                top: auto;
                bottom: -36px;
            }
            .popup-desc::after{
                transform: rotate(180deg);
                top: auto;
                bottom: calc(100% - 1px);
            }
        }
    }
}
@media(max-width: 1770px){
    .header-fixed{
        .navList{
            width: 1000px;
        }
    }
}
@media(max-width: 1650px) {
    .header-fixed {
        &__inner .list-line li {
            padding: 14px 7px 13px;
        }
        &__inner .header__bottom-right .Btn-Flamenco {
            margin-right: 10px;
        }
        &__inner .logo  {
            margin-right: 200px;
        }
        &__inner .contact a {
            margin-right: 10px;
        }
        .header__bottom-left .menu> li:first-child> i {
            margin-right: 10px;
        }
    }
    .header__bottom .header__bottom-right form {
        flex-grow: 1;
    }
    .header__bottom .header__bottom-right {
        flex-grow: 1;
        margin-left: 30px;
    }
    .header-fixed__inner .list-line li .logIn .popup-desc{
        left: auto;
        right: -34px;
        &::after{
            left: 32px;
        }
    }
}
@media(max-width: 1300px){
    .header-fixed{
        &__inner{
             .contact a{
                 margin-right: 10px;
             }
             .list-line li {
                    padding: 14px 14px 13px;
             }
        }
    }
}
@media(max-width: 1600px){
    .header-fixed{
        .navList{
            width: 790px;
        }
    }
}
@media(max-width: 1500px){
    .header{
        &__top{
            &-right{
                .contact{
                    margin-left: 0;
                    margin-right: 10px;
                }
                .list-line {
                    li{
                        padding: 25px 12px;
                        &:last-of-type{
                            padding-right: 0!important;
                        }
                    }
                }
                .basket {
                    .popup-desc{
                        bottom: -27px;
                    }
                }
                .logIn{
                    span:not(.popup-desc){
                        display: none;
                    }
                    i{
                        margin-right: 0;
                    }
                    .popup-desc{
                        left: auto;
                        right: -26px;
                        bottom: -40px;
                        &::after{
                            left: auto;
                            right: 29px;
                        }
                    }
                }
            }
            &-left{
                .logo{
                    width: 182px;
                }
            }
        }
        &__bottom{
            &-right{
                form{
                    width: 29.177vw;
                }
            }
        }
    }
}
@media(max-width: 1380px){
    .header{
        &__bottom{
            &-left {
                .menu{
                    &> li:first-child{
                        &> .dropdown{
                            width: 310px;
                        }
                    }
                }
            }
        }
        &-fixed{
            .navList{
                width: 655px;
                .thirdColumn{
                    margin-left: 10px;
                }
            }
        }
    }
}
@media(max-width: 1220px) {
    .header-fixed__inner .logo {
        margin-right: 100px;
    }
}
@media(max-width: 1199px){
    .BtnMenu{
        display: block;
        cursor: pointer;
    }
    .header{
        &__top{
            .menu-wrap{
                display: none;
            }
            &-right{
                .contact{
                    margin-right: 20px;
                }
                .list-line li{
                    padding-right: 16px;
                    padding-left: 16px;
                }
            }
        }
        &__bottom{
            &-left{
                display: none;
            }
            &-right{
                width: 100%;
                form{
                    width: calc(100% - 147px);
                }
                .input-wrap{
                    margin-left: 10px;
                }
            }
        }
        &-fixed{
            &__inner{
                .BtnMenu{
                    display: block;
                    cursor: pointer;
                    i{
                        color: var(--White);
                    }
                }
                .header__bottom-right{
                    width: auto;
                    .Btn-Flamenco{
                        margin-right: 20px;
                    }
                }
               /* .list-line li{
                    padding: 18px 24px 14px;
                    border-right: 1px solid rgba(232, 232, 232, .5);
                    &:not(:first-of-type){
                        display: none;
                    }
                }*/
                .header__bottom-left{
                    nav{
                        display: none;
                    }
                }
            }
        }
    }
    .header__bottom .header__bottom-right {
        margin-left: 0;
    }
}
@media(max-width: 1170px) {
    .header-fixed__inner .logo {
        margin-right: 10px;
    }
    .header-fixed .contact span {
        display: none;
    }
    .header-fixed__inner .header__bottom-right {
        flex-grow: 0;
    }
}
@media(max-width: 991px){
    .header{
        &__top{
            &-right{
                .list-line{
                    li{
                        border-left: 0;
                        padding-right: 0;
                        &:not(:nth-last-child(2)){
                            display: none;
                        }
                    }
                }
                .contact{
                    margin-right: 10px;
                }
            }
        }
        &-fixed{
            &__inner{
                .list-line li{
                    padding: 18px 24px 14px;
                    border-right: 1px solid rgba(232, 232, 232, .5);
                    &:not(:first-of-type){
                        display: none;
                    }
                }
            }
        }
    }
}
@media(max-width: 767px){
    .header{
        &__top{
            .logo{
                width: 160px;
            }
            &-right {
                .list-line{
                     li{
                         padding-left: 0;
                     }
                }
            }
        }
    }
    .BtnMenu{
        margin-right: 15px;
    }
}
@media(max-width: 600px){
    .header{
        &__top{
            .logo{
                width: 133px;
            }
            &-right{
                .contact{
                    display: flex;
                    align-items: center;
                    margin-right: 0;
                    a{
                        margin-bottom: 0;
                        &:not(:first-of-type) i {
                            font-size: 16px;
                        }
                        i{
                            font-size: 21px;
                        }
                        span{
                            display: none;
                        }
                    }
                }
                .basket{
                    &__inner{
                        span:not(.show-600){
                            display: none;
                        }
                        span.show-600{
                            margin-bottom: 0;
                        }
                    }
                }
                .list-line li{
                    padding-bottom: 22px;
                }
            }
        }
        &__bottom{
            &-right{
                position: relative;
                form{
                    width: 38px;
                }
                .input-wrap{
                    position: static;
                    .input[type="search"]{
                        padding-left: 8px;
                        padding-right: 30px;
                        position: absolute;
                        top: 9px;
                        right: 0;
                        width: 38px;
                        &:focus{
                            width: 100%;
                        }
                    }
                    .searchBtn{
                        right: 10px;
                        top: 19px;
                    }
                }
                .Btn-Flamenco{
                    width: calc(100% - 50px);
                    text-align: center;
                }
            }
        }
        &-fixed{
            &__inner {
                .header__bottom-right .Btn-Flamenco{
                    display: none;
                }
                .contact {
                    a{
                        margin-right: 0;
                        &:not(:first-of-type) i {
                            font-size: 16px;
                        }
                        i{
                            font-size: 21px;
                        }
                    }
                }
                .list-line {
                    li{
                        border: none;
                        padding-right: 0;
                        padding-left: 0;
                        .basket {
                            .popup-desc{
                                left: auto;
                                right: -14px;
                                bottom: -47px;
                                &::after{
                                    left: 33px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
    .basket.not-empty {
        position: relative;
        overflow: visible;
    }
    .header__top, .header-fixed{
        .basket__inner {
            position: absolute;
            right: 0;
            top: -10px;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 22px;
            height: 22px;
            background: white;
            border-radius: 50%;
            span:not(.popup-desc) {
                color: var(--Flamenco) !important;
                font-size: 12px;
                margin-bottom: 0;
            }
            
        }
    }
    .header__top {
        .basket__inner {
            background: var(--Wedgewood);
            span:not(.popup-desc) {
                color: var(--White) !important;
                font-size: 12px;
            }
        }
    }
}